</template>
<script>

  import {MessageBox, Popup} from 'mint-ui';
  import moment from 'moment'

  let df = 'HH:mm:ss';
  export default {
    data(){
      return {
        punchCardInfo: {//
          locations: [],
          punchCardLogs: []
        },
        punchCardSuccess: {},// 
        showBtnContent: false,// 
        qulocation: false, //
        failModel: false,// 
        failModelErr: false,// 
        wifiPopup: false, // wifi
        popupVisible: false,//
        wifiIP: '',// wifiIP
        MyPosition: {},//
        objNr: {},// 
        year: new Date().getFullYear(), //
        month: new Date().getMonth() + 1, //
        date: new Date().getDate(), //
        hour: 0,// 
        minute: 0,// 
        second: 0,// 
        imgSrc: {
          header: require('../../assets/tx.png'), // 
          PostionIcon: require('../../assets/ico_location_1.png'), // 
          bg: require('../../assets/0_gif.gif'), // 1
          bg1: require('../../assets/0_gif1.gif'), // 2
          alertHeader: require('../../assets/pic_check in.png'), // 
        },
        outsideObtainValue: true, //truefalse
        searchLocationArray: [],  //locations
        twRange: '', //
      }
    },
    created(){
      this.doSearch(); //
    },
    methods: {
      //
      doSearch(state){
        this.handerSign();
        setInterval(this.handerSign, 1000);
        let imageString = this.getCookie('avatarImages'); //
        this.objNr = {
          fetchPostion: this.getCookie('infoObjPassPostion'),
          fetchName: this.getCookie('infoObjPassName'),
        };
        if (imageString) {
          this.imgSrc.header = imageString;
        }
        this.$http.post('/api/v1.0/client/findPunchCardLog').then(response => { //
          if (response.body.code === 200) {
            this.punchCardInfo = response.body.result;
            if (this.punchCardInfo.attendRuleUid === '4') {
              MessageBox('', '');
              this.$router.push({path: '/signCard'});
            }
            if (state) {
              this.punchCardSuccess = JSON.parse(JSON.stringify(this.punchCardInfo));
              this.punchCardSuccess.punchCardLogs = this.punchCardInfo.punchCardLogs.length ? this.punchCardInfo.punchCardLogs[this.punchCardInfo.punchCardLogs.length - 1] : {};
              this.popupVisible = true;
              this.showBtnContent = false;
            }
          } else if (response.body.code === 500) { //
            MessageBox('', response.body.message);
            if (response.body.message === '') {
              this.$router.push({path: '/signCard'});
            }
          }
        }, response => {
//          console.log('error callback');
        });
      },
      //
      // 
      handerSign(){
        let oDate = new Date();
        this.hour = AddZero(oDate.getHours());
        this.minute = AddZero(oDate.getMinutes());
        this.second = AddZero(oDate.getSeconds());
        function AddZero(n) {
          if (n < 10) {
            return '0' + n;
          }
          return '' + n;
        }
      },
      // 
      punchClock(state){
        return typeof(state) === 'number' && state >= 0;
      },
      //
      punchTime(time){
        return time ? moment(time).format(df) : '';
      },
      handerClickEvent(){  //   
        if (navigator.onLine) { //
          if (this.punchCardInfo.isWifi) {
            this.wifiPopup = true;// wifi
          }
          this.wifiIP = '';
          this.qulocation = true;
        } else { //
          MessageBox('', '');
        }
      },
      closeAlert(){ //alert
        this.qulocation = false;
        // 
        setTimeout(() => {
          this.failModel = false;
          this.wifiPopup = false;
        }, 300);
      },
      knowFunction(){
        this.popupVisible = false;
        this.qulocation = false;
      },
      //
      submitApplyRouter(type){
        this.setCookie('leaveType', type, 365);
        this.$router.push({path: '/leave'});
      },
      //wifi
      okClickWifi(){
        this.wifiPopup = false;
        this.qulocation = true;
      },
      // 
      okClickEvent(){
        this.showBtnContent = true;
        this.qulocation = false;
        let BMap = null;
        let map = null;
        let self = this;
        let curl;
        //
        let u = navigator.userAgent;
        let isAndroid = '0';
        isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android
        if (isAndroid) {
          curl = {
            location: location.href //
          };
        }
        if (!isAndroid) {
          curl = {
            location: location.href.toString().split('#')[0] //
          };
        }
        //
        new Promise((resolve, reject) => {
          window._initBaiduMap = function () {
            window.document.body.removeChild($script);
            window._initBaiduMap = null;
            resolve();
            BMap = window.BMap;
            map = new BMap.Map();
          };
          // 
          const $script = document.createElement('script');
          window.document.body.appendChild($script);
          $script.src = `//api.map.baidu.com/api?v=2.0&ak=FRMO4GzB3wRlgFrAURcQSKWdZmzHuuD4&callback=_initBaiduMap`;
          // ip
          const $scripts = document.createElement('script');
          window.document.body.appendChild($scripts);
          $scripts.src = "https://pv.sohu.com/cityjson?ie=utf-8";
        }).then(() => {
          this.$http.post('/api/v1.0/wechat/sign', curl).then(response => { //
            if (response.body.code === 200) {
              this.t1 = response.body.result.appid.toString();
              this.t2 = response.body.result.timestamp.toString();
              this.t3 = response.body.result.nonceStr.toString();
              this.t4 = response.body.result.signature.toString();
              this.yyy = true;
              let cvt = new BMap.Convertor();
              wx.config({
                debug: false,
                appId: this.t1,
                timestamp: this.t2,
                nonceStr: this.t3,
                signature: this.t4,
                jsApiList: [
                  'getLocation'
                ]
              });
              wx.error(function (res) {
                // 
                self.failModelErr = true;
                self.failModel = true;
                self.qulocation = true;
                self.showBtnContent = false;
              });
              wx.ready(function () {
                wx.getLocation({
                  type: 'wgs84',
                  success: function (res) {
                    self.getLocations = false;
                    self.latitude = res.latitude; // 90 ~ -90           res.latitude;
                    self.longitude = res.longitude; // 180 ~ -180     res.longitude;  //
                    let speed = res.speed; // /
                    let accuracy = res.accuracy; // 
                    cvt.translate([new BMap.Point(res.longitude, res.latitude)]/*  wx */, 1, 5, (data) => {   //
                      if (data.status === 0) {
                        let myPosition = data.points[0]; // 
                        self.MyPosition = myPosition;
//                        self.$http.post('/api/v1.0/client/findPunchCardLog').then(response => { //
//                          if (response.body.code === 200) {
                        //start
                        let scopes = [];
                        self.searchLocationArray = (rt => {
                          if (!rt) return false;
                          let out = [];
                          rt.forEach(item => {
                            out.push(new BMap.Point(item.LONGITUDE, item.LATITUDE));
                            scopes.push(item.SCOPE);
                          });
                          return out;
                        })(self.punchCardInfo.locations);

                        let arrayLonglat = self.searchLocationArray;
                        cvt.translate(arrayLonglat/*  */, 3, 5, (data) => {
                          if (data.status === 0) {
                            // 
                            let distance;
                            self.twRange = '';
                            self.outsideObtainValue = true;// true
                            for (let i = 0; i < data.points.length; i++) {
                              distance = map.getDistance(data.points[i], myPosition);
                              if (distance < scopes[i]) {
                                self.outsideObtainValue = false;
                                break;
                              }
                            }
                            if (self.outsideObtainValue) {
                              new BMap.Geocoder().getLocation(myPosition, function (res) { //
                                self.twRange = res.addressComponents.district + res.addressComponents.street;
                                if (!self.twRange) {
                                  self.twRange = '';
                                }
                                self.punchInfo(self, self.twRange);
                              });
                            } else {
                              self.punchInfo(self, self.twRange);
                            }
                          } else {
                            this.showBtnContent = false;
                            MessageBox('', '');
                          }
                        });
                      } else {
                        this.showBtnContent = false;
                        MessageBox('', '');
                      }
                    });
                  },
                  cancel: function (res) {
                    //
                    let u = navigator.userAgent;
                    let isAndroid = '0';
                    isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android
                    self.showBtnContent = false;
                    if (isAndroid) {
                      MessageBox('', '');
                    }
                    if (!isAndroid) {
                      MessageBox('', '');
                    }
                    //
                  },
                  fail: function (res) {
                    // 
                    self.failModelErr = false;
                    self.failModel = true;
                    self.qulocation = true;
                    self.showBtnContent = false;
                  }
                });
              });
            } else {
              this.showBtnContent = false;
              MessageBox('', response.body.message);
            }
          }, response => {
            console.log('error callback');
          });
        });
        //
      },
      // ip
      getIP(){
        if (returnCitySN["cip"]) {
          this.wifiIP = returnCitySN["cip"];
        } else {
          this.getIP();
        }
      },
      punchInfo(self, twRange){
        // ip
        if (this.punchCardInfo.isWifi) {
          this.getIP();
        }
        let updakaObj;
        if (!this.punchCardInfo.punchCardLogs.length) {
          this.punchCardInfo.punchCardLogs.push({
            twStatus: '',
            owStatus: ''
          })
        }
        let position = this.punchCardInfo.punchCardLogs.length - 1;
        if (!this.punchClock(this.punchCardInfo.punchCardLogs[position].twStatus)) {
          updakaObj = {
            isRange: this.outsideObtainValue ? true : 0,
            location: twRange,
            longitude: this.MyPosition.lng,
            latitude: this.MyPosition.lat,
//            wifi: this.wifiIP
          }
        } else if (this.punchClock(this.punchCardInfo.punchCardLogs[position].owStatus) && !this.punchClock(this.punchCardInfo.punchCardLogs[position].twStatus)) {
          updakaObj = {
            isRange: this.outsideObtainValue ? true : 0,
            location: twRange,
            longitude: this.MyPosition.lng,
            latitude: this.MyPosition.lat,
//            wifi: this.wifiIP
          }
        }
        this.$http.post('/api/v1.0/client/punchCardLog', updakaObj).then(response => { //
          if (response.body.code === 200) {
            this.doSearch(true);
          } else if (response.body.code === 500) {
            MessageBox('', response.body.message);
          }
        }, response => {
        });
      }
    },
  }

</script>

