                  <el-form-item v-if="item.fieldType===1" :label="item.remark" :prop="item.uid+'[0].value'" :rules=" [{required: item.isrequired,message: ''+item.remark+'('+textType[item.javaValidexp]+')', pattern: patt[item.javaValidexp], trigger: 'blur'},
            {message: `${item.characterLimit}`,max:item.characterLimit, trigger: 'blur'}
          ]">
                    <el-input :disabled="!item.isedit" :placeholder="''+item.remark"
                              v-model="staffInfoName[item.uid][0].value"></el-input>
                  </el-form-item>
                  <!---->
                  <el-form-item v-if="item.fieldType===2" :label="item.remark" :prop="item.uid+'[0].value'"
