  Vue.use(Col);
  Vue.use(Upload);
  import utils from "@/components/utils";
  import {MessageBox, Indicator} from "mint-ui";
  import uploadImage from "./uploadImage";

  // ========
  let df1 = "YYYY-MM-DD";
  let df2 = "YYYY-MM";
  let pickerslot = document.getElementsByClassName("picker-slot");
  // ========
  export default {
    data() {
      return {
        tokenHeader: {
          charset: "utf-8",
          openId: this.getCookie("openId")
        },
        textType: {
          "1": "",
          "2": "",
          "3": "",
          "4": "",
          "12": "",
          "13": "",
          "14": "",
          "23": "",
          "24": "",
          "34": "",
          "123": "",
          "124": "",
          "134": "",
          "234": "",
          "1234": "",
          type3: ""
        },
        patt: {
          "1": /^[\u4e00-\u9fa5]+$/,
          "2": /^[A-Za-z]+$/,
          "3": /^\d+$/,
          "4": /^[|\[\],.?'"\(\)+\-_*\/\\&\$#^@!%~`<>:;=\{\}\s]+$/,
          "12": /^[\u4e00-\u9fa5A-za-z]+$/,
          "13": /^[\u4e00-\u9fa5\d]+$/,
          "14": /^[\u4e00-\u9fa5|\[\],.?'"\(\)+\-_*\/\\&\$#^@!%~`<>:;=\{\}\s]+$/,
          "23": /^[A-Za-z\d]+$/,
          "24": /^[A-Za-z|\[\],.?'"\(\)+\-_*\/\\&\$#^@!%~`<>:;=\{\}\s]+$/,
          "34": /^[\d|\[\],.?'"\(\)+\-_*\/\\&\$#^@!%~`<>:;=\{\}\s]+$/,
          "123": /^[\u4e00-\u9fa5A-Za-z\d]+$/,
          "124": /^[\u4e00-\u9fa5A-Za-z|\[\],.?'"\(\)+\-_*\/\\&\$#^@!%~`<>:;=\{\}\s]+$/,
          "134": /^[\u4e00-\u9fa5\d|\[\],.?'"\(\)+\-_*\/\\&\$#^@!%~`<>:;=\{\}\s]+$/,
          "234": /^[A-Za-z\d|\[\],.?'"\(\)+\-_*\/\\&\$#^@!%~`<>:;=\{\}\s]+$/,
          "1234": /^[\u4e00-\u9fa5A-Za-z\d|\[\],.?'"\(\)+\-_*\/\\&\$#^@!%~`<>:;=\{\}\s]+$/,
          type3: /^[1-9][0-9]*$/
        },
        // ==========
        startDate: "",
        nowDateTime: new Date(),
        selectDateTime: "",
        dateType: 0,
        type: 0,
        pos: 0, // 
        // ==========
        resumeEdit: false, // 
        bankCardEdit: false, //
        informEdit: false, // 
        imageScale: false, // 
        imageScaleUrl: "", //
        selected: "1",
        status: false,
        isLowEntry: false,
        isEntry: false,
        staticWorkCity: "",
        publicParams: null,
        labelWidth: "140px",
        nativePlaces: null,
        bankNames: null,
        staffStatus: null, //
        maritalStatuses: null,
        nations: null,
        states: null, //
        politicsStatuses: null,
        provinces: null,
        podoCities: null,
        typeOfDemiciles: null,
        residenceCities: null,
        poreCities: null,
        maxinumDeucaLevels: null,
        diplomaTypes: null,
        probations: null,
        contractTypes: null,
        recruitmentChannels: ["", "51job", "", "", "boss "],
        staffInfo: [], // 
        staffInfoName: {}, // 
        staffRecordInfo: [], //
        staffShareOptionInfo: [], // 
        reportPerson: "", //
        rulesStaffInfoName: {
          accfuNum: [
            {message: "()", trigger: "blur", pattern: /^\d+$/},
            {message: "24", trigger: "blur", max: 24}
          ], //
          dateOfBirth: [
            {
              type: "date",
              message: "",
              trigger: "change",
              validator: this.isDate
            }
          ], //
          emplsepacertUrl: [{message: "", trigger: "blur"}], //
          englishName: [
            {message: "", trigger: "blur"},
            {message: "128", trigger: "blur", max: 128}
          ], //
          finallyEmpCom: [
            {message: "", trigger: "change"},
            {message: " 255 ", trigger: "blur", max: 255}
          ], //
          gender: [{type: "number", message: "", trigger: "change"}], //
          idcard: [
            {
              message: "",
              trigger: "blur",
              pattern: /^\d{17}(?:\d|[Xx])$/
            }
          ], //
          informUrl: [{type: "array", message: "", trigger: "blur"}], //
          maritalStatus: [{message: "", trigger: "blur"}], //
          mobile: [
            {message: "", trigger: "blur", pattern: /^1\d{10}$/}
          ], //
          name: [
            {
              min: 2,
              max: 32,
              message: "( 2  32 )",
              trigger: "change"
            }
          ], //
          nation: [{message: "", trigger: "blur"}], //
          personalEmail: [
            {type: "email", message: "", trigger: "change", max: 30}
          ], //
          qq: [
            {message: " QQ ()", trigger: "blur", pattern: /^\d+$/},
            {message: "24", trigger: "blur", max: 24}
          ], //qq
          resumeUrl: [{type: "array", message: "", trigger: "blur"}], //
          socsecNum: [
            {message: "()", trigger: "blur", pattern: /^\d+$/},
            {message: " 24 ", trigger: "blur", max: 24}
          ], //
          staffPhoUrl: [{message: "", trigger: "blur"}], //
          technicalTitle: [
            {message: "", trigger: "change"},
            {message: "32", trigger: "blur", max: 32}
          ], //
          theArcIns: [
            {message: "", trigger: "change"},
            {message: "128", trigger: "blur", max: 128}
          ], //
          wechart: [
            {
              message: "",
              trigger: "blur",
              pattern: /^[a-zA-Z0-9-_]+$/
            },
            {message: "620", trigger: "blur", min: 6, max: 20}
          ] //
        }
      };
    },
    created: function () {
    },
    computed: {},
    methods: {
      queryStaffInfo() {
        this.$http
          .get("/api/v1.0/client/selectStaff")
          .then(res => {
            res = res.body;
            if (res.code === 200) {
              if (res.result.staffStaus === -2) {
                this.isLowEntry = true;
              } else {
                this.isLowEntry = false;
              }

              if (res.result.staffStaus === -1 || res.result.staffStaus === -2) {
                this.isEntry = true;
              } else {
                this.isEntry = false;
              }

              let configured = this.isEntry ? "noentIsconfig" : "isconfig";

              res.result.staffs.forEach(item => {
                switch (item.tname) {
                  case "STAFF":
                    if (item[configured]) {
                      this.staffInfo.push(item);
                      if (item.isDefined) {
                        if (item.fieldType === 6) {
                          this.staffInfoName[item.uid] = {
                            value: item.value,
                            arr: []
                          };
                          item.value.forEach(v => {
                            if (v.value)
                              this.staffInfoName[item.uid].arr.push(v.value);
                          });
                        } else if (item.fieldType === 7 || item.fieldType === 8) {
                          this.staffInfoName[item.uid] = {
                            info: item,
                            edit: false
                          };
                          this.staffInfoName[item.uid].info.value.forEach(v => {
                            v.state = false;
                          });
                        } else {
                          this.staffInfoName[item.uid] = item.value;
                        }
                      } else {
                        this.staffInfoName[item.jname] = item.value;
                      }
                    }
                    break;
                  case "STAFF_RECORD":
                    this.staffRecordInfo.push(item);
                    break;
                  case "STAFF_SHARE_OPTION":
                    this.staffShareOptionInfo.push(item);
                    break;
                }
              });
              if (
                this.staffInfoName["podoMessage"] &&
                typeof this.staffInfoName["podoMessage"].typeOfDemicile ===
                "number"
              )
                this.staffInfoName["podoMessage"].typeOfDemicile =
                  this.staffInfoName.podoMessage.typeOfDemicile + "";
              if (
                this.staffInfoName["nativePlace"] &&
                typeof this.staffInfoName["nativePlace"].nativePlace === "number"
              )
                this.staffInfoName["nativePlace"].nativePlace =
                  this.staffInfoName.nativePlace.nativePlace + "";
              if (
                this.staffInfoName["politicsStatus"] &&
                typeof this.staffInfoName["politicsStatus"].politicsStatus ===
                "number"
              )
                this.staffInfoName["politicsStatus"].politicsStatus =
                  this.staffInfoName.politicsStatus.politicsStatus + "";
              if (typeof this.staffInfoName["maritalStatus"] === "number")
                this.staffInfoName["maritalStatus"] =
                  this.staffInfoName.maritalStatus + "";
              if (
                this.staffInfoName["eduInfor"] &&
                typeof this.staffInfoName["eduInfor"].diplomaType === "number"
              )
                this.staffInfoName["eduInfor"].diplomaType =
                  this.staffInfoName["eduInfor"].diplomaType + "";
              if (this.staffInfoName["informUrl"])
                this.staffInfoName["informUrl"].forEach(item => {
                  item.state = false;
                });
              if (this.staffInfoName["resumeUrl"])
                this.staffInfoName["resumeUrl"].forEach(item => {
                  item.state = false;
                });
              if (
                this.staffInfoName.bankName &&
                this.staffInfoName.bankName.staffCardUrls
              )
                this.staffInfoName.bankName.staffCardUrls.forEach(item => {
                  item.state = false;
                });
              if (
                this.staffInfoName["podoMessage"] &&
                this.staffInfoName["podoMessage"].podoProvince
              )
                this.queryCities(
                  this.staffInfoName["podoMessage"].podoProvince,
                  "podoMessage"
                );
              if (
                this.staffInfoName["ResperMessage"] &&
                this.staffInfoName["ResperMessage"].residenceProvince
              )
                this.queryCities(
                  this.staffInfoName["ResperMessage"].residenceProvince,
                  "ResperMessage"
                );
              if (
                this.staffInfoName["poreLocation"] &&
                this.staffInfoName["poreLocation"].poreProvince
              )
                this.queryCities(
                  this.staffInfoName["poreLocation"].poreProvince,
                  "poreLocation"
                );
              if (this.staffInfoName["idcard"])
                this.staffIdcard(this.staffInfoName["idcard"], false);
              Indicator.close(); //
              this.status = true;
            }
          })
          .catch(err => console.log(err));
      },
      // 
      workAge(data) {
        if (data) {
          let ta =
            (new Date().getTime() - +moment(data).format("x")) / 31536000000;
          if (ta < 0) return "0.0";
          return ta.toFixed(1);
        } else {
          return "0.0";
        }
      },
      // 
      datefmt(str) {
        if (str) return moment(str).format(df1);
        else return "";
      },
      datefmt1(str) {
        if (str) return moment(str).format(df2);
        else return "";
      },
      //
      contracTime(data) {
        if (data.type === 0) {
          return data.state ? "" : "";
        } else if (data.type === 1) {
          return data.state ? "" : "";
        } else if (data.type === 2) {
          return data.state ? "" : "";
        } else if (data.type === 3) {
          return data.state ? "" : "";
        } else {
          return data.state ? "" : "";
        }
      },
      // 
      staffIdcard(data, state) {
        if (/^\d{17}(?:\d|[Xx])$/g.test(data.toString().trim())) {
          this.staffInfoName.dateOfBirth = new Date(
            `${data.substring(6, 10)}-${data.substring(10, 12)}-${data.substring(
              12,
              14
            )}`
          ).getTime();
          this.staffInfoName.gender =
            +data.toString().trim()[16] % 2 === 0 ? 0 : 1;
        }
        if (state) {
          this._updateValue("gender" || "dateOfBirth");
        }
      },
      //
      queryCities(pid, type) {
        if (!pid) {
          return;
        }
        this.$http
          .get(`/api/v1.0/common/query/city/${pid}`)
          .then(res => {
            res = res.body;
            if (res.code === 200) {
              if (type === "podoMessage") {
                this.podoCities = res.result;
              } else if (type === "ResperMessage") {
                this.residenceCities = res.result;
              } else if (type === "poreLocation") {
                this.poreCities = res.result;
              }
            }
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 
      getPC(pid, cid) {
        let p = "";
        this.provinces.forEach(v => {
          if (pid.toString() === v.uid) {
            p = v.name;
            this.$http.get(`/api/v1.0/common/query/city/${pid}`).then(res => {
              res = res.body;
              if (res.code === 200) {
                res.result.forEach(v => {
                  if (v.uid === cid.toString()) {
                    this.staticWorkCity = v.name;
                    return false;
                  }
                });
              }
            });
            return false;
          }
        });
        return p;
      },
      //
      addChild() {
        this.staffInfoName.hasChilds.childs.push({
          accountUid: null,
          age: null,
          birthCertifUrl: null,
          companyUid: null,
          dateOfBirth: null,
          gender: 1,
          name: null,
          staffUid: null,
          uid: null
        });
      },
      //
      rmvChild(item) {
        let index = this.staffInfoName.hasChilds.childs.indexOf(item);
        if (index !== -1) {
          this.staffInfoName.hasChilds.childs.splice(index, 1);
        }
      },
      //
      addContact() {
        this.staffInfoName.emergencyContact.push({
          accountUid: null,
          companyUid: null,
          emergContact: null,
          emergContactPhone: null,
          staffUid: null,
          uid: null
        });
        this._updateValue("emergencyContact");
      },
      //
      rmvContact(item) {
        let index = this.staffInfoName.emergencyContact.indexOf(item);
        if (index !== -1) {
          this.staffInfoName.emergencyContact.splice(index, 1);
          this._updateValue("emergencyContact");
        }
      },
      // 
      isDates(rule, value, callback) {
        let arr = rule.field.split(".");
        if (arr[0] === "eduInfor") arr[1] = "entSchst";
        else if (arr[0] === "ResperMessage") arr[1] = "resperst";
        if (value) {
          if (this.staffInfoName[arr[0]][arr[1]]) {
            if (value > this.staffInfoName[arr[0]][arr[1]]) {
              callback();
            } else {
              if (arr[0] === "eduInfor")
                callback(new Error((rule.message = "")));
              else if (arr[0] === "ResperMessage")
                callback(new Error((rule.message = "")));
            }
          }
        } else {
          if (rule.required) callback(new Error(rule.message));
          else callback();
        }
      },
      //
      _updateValue(name) {
        this.staffInfo.forEach((item, index) => {
          if (item.isDefined) {
            if (item.uid === name) {
              this.$set(this.staffInfo, index, this.staffInfo[index]);
            }
          } else {
            if (item.jname === name) {
              this.$set(this.staffInfo, index, this.staffInfo[index]);
            }
          }
        });
      },
      // 
      _upload(data) {
        let arr = data.info.name.split(".");
        if (arr.length === 1) {
          if (data.info.type === 0) {
            this.staffInfoName[arr[0]] = data.url;
          } else if (data.info.type === 1) {
            this.staffInfoName[arr[0]].push({
              uid: null,
              staffUid: null,
              url: data.url,
              state: false
            });
          } else if (data.info.type === 7 || data.info.type === 8) {
            if (this.staffInfoName[arr[0]].info.value.length > 0) {
              if (this.staffInfoName[arr[0]].info.value[0].value) {
                this.staffInfoName[arr[0]].info.value.push({
                  accountUid: null,
                  companyUid: null,
                  staffConfigUid: null,
                  staffUid: null,
                  state: false,
                  uid: null,
                  value: data.url
                });
              } else {
                this.staffInfoName[arr[0]].info.value[0].value = data.url;
              }
            } else {
              this.staffInfoName[arr[0]].info.value.push({
                accountUid: null,
                companyUid: null,
                staffConfigUid: null,
                staffUid: null,
                state: false,
                uid: null,
                value: data.url
              });
            }
          }
        } else if (arr.length === 2) {
          if (data.info.type === 0) {
            this.staffInfoName[arr[0]][arr[1]] = data.url;
          } else if (data.info.type === 2) {
            this.staffInfoName[arr[0]][arr[1]].push({
              uid: null,
              staffUid: null,
              url: data.url,
              state: false
            });
          }
        } else if (arr.length === 3) {
          this.staffInfoName[arr[0]][arr[1]][data.info.position][arr[2]] =
            data.url;
        }
        this._updateValue(arr[0]);
      },
      // 
      resumeDel(name, edit) {
        let arr = name.split(".");
        let obj;
        if (arr.length === 1) {
          if (typeof edit === "string") {
            obj = this.staffInfoName[arr[0]];
          } else {
            obj = this.staffInfoName[arr[0]].info.value;
          }
        } else if (arr.length === 2) {
          obj = this.staffInfoName[arr[0]][arr[1]];
        }
        for (let i = 0; i < obj.length; i++) {
          if (obj[i].state) {
            obj[i].state = false;
          }
        }
        if (typeof edit === "string") {
          this[edit] = true;
        } else {
          this.staffInfoName[arr[0]].edit = true;
        }
        this._updateValue(arr[0]);
      },
      // 
      resumeConfirm(name, edit) {
        let arr = name.split(".");
        let obj;
        if (arr.length === 1) {
          if (typeof edit === "string") {
            obj = this.staffInfoName[arr[0]];
          } else {
            obj = this.staffInfoName[arr[0]].info.value;
          }
        } else if (arr.length === 2) {
          obj = this.staffInfoName[arr[0]][arr[1]];
        }
        for (let i = 0; i < obj.length; i++) {
          if (obj[i].state) {
            obj.splice(i, 1);
            i--;
          }
        }
        if (typeof edit === "string") {
          this[edit] = false;
        } else {
          this.staffInfoName[arr[0]].edit = false;
        }
        this._updateValue(arr[0]);
      },
      // 
      resumeCancel(edit, name) {
        if (typeof edit === "string") {
          this[edit] = false;
        } else {
          this.staffInfoName[name].edit = false;
        }
        this._updateValue(name);
      },
      //
      imageSelect(data, name, state) {
        let arr = name.split(".");
        let obj;
        if (arr.length === 1) {
          if (state === 4 || state === 5) {
            obj = this.staffInfoName[arr[0]].info.value[data];
          } else {
            obj = this.staffInfoName[arr[0]][data];
          }
        } else if (arr.length === 2) {
          obj = this.staffInfoName[arr[0]][arr[1]][data];
        }
        if (obj.state) obj.state = false;
        else obj.state = true;
      },
      // 
      imageScaleOpen(data, state, index, uid) {
        if (
          state === 0 ||
          (state === 1 && !this.resumeEdit) ||
          (state === 2 && !this.bankCardEdit) ||
          (state === 3 && !this.informEdit) ||
          (state === 4 && !this.staffInfoName[uid].edit)
        ) {
          if (this.isFormatImg(data)) {
            this.imageScale = true;
            this.imageScaleUrl = data;
          }
        } else {
          // 
          if (state === 1) {
            this.imageSelect(index, "resumeUrl");
            this._updateValue("resumeUrl");
          } else if (state === 2) {
            this.imageSelect(index, "bankName.staffCardUrls");
          } else if (state === 3) {
            this.imageSelect(index, "informUrl");
            this._updateValue("informUrl");
          } else if (state === 4) {
            this.imageSelect(index, uid, state);
            this._updateValue(uid);
          } else if (state === 5) {
            this.imageSelect(index, uid, state);
            this._updateValue(uid);
          }
        }
      },
      //
      imageScaleClose() {
        this.imageScale = false;
      },
      // 
      queryProvinces() {
        this.$http
          .get("/api/v1.0/common/query/province")
          .then(res => {
            res = res.body;
            if (res.code === 200) {
              this.provinces = res.result;
            }
          })
          .catch(err => {
            console.log(err);
          });
      },
      //
      isFormatImg(url) {
        return utils.isImage({name: url});
      },
      //
      isDate(rule, value, callback) {
        if (rule.required) {
          if (value) callback();
          else callback(new Error(rule.message));
        } else {
          callback();
        }
      },
      // 
      isSize(rule, value, callback, source, options) {
        if (!value) {
          callback();
          return;
        }
        let n = +value;
        if (n > 0 && n <= Math.pow(10, rule.max) - 1) {
          callback();
        } else {
          callback(new Error(rule.message));
        }
      },
      //
      checkList(data) {
        this._updateValue(data);
      },
      //
      fileUrl(data) {
        return data.replace("common", "client");
      },
      //
      save() {
        let flag = 0;
        this.$refs.personFm.validate(valid => {
          if (!valid) {
            flag++;
            return false;
          }
        });
        if (flag) {
          return;
        }
        Indicator.open("...");
        if (this.staffInfoName["informUrl"])
          this.staffInfoName["informUrl"].forEach(item => {
            delete item.state;
          });
        if (this.staffInfoName["resumeUrl"])
          this.staffInfoName["resumeUrl"].forEach(item => {
            delete item.state;
          });
        if (
          this.staffInfoName.bankName &&
          this.staffInfoName.bankName.staffCardUrls
        )
          this.staffInfoName.bankName.staffCardUrls.forEach(item => {
            delete item.state;
          });
        if (this.staffInfo)
          this.staffInfo.forEach(item => {
            if (item.isDefined) {
              if (item.fieldType === 6) {
                let values = [];
                this.staffInfoName[item.uid].arr.forEach(v => {
                  values.push({
                    accountUid: null,
                    companyUid: null,
                    staffConfigUid: null,
                    staffUid: null,
                    uid: null,
                    value: v
                  });
                });
                item.value = values;
              } else if (item.fieldType === 7 || item.fieldType === 8) {
                item.value = this.staffInfoName[item.uid].info.value;
                item.value.forEach((ite, ind) => {
                  delete ite.state;
                  if (!ite.value) {
                    item.value.splice(ind, 1);
                  }
                });
              } else {
                item.value = this.staffInfoName[item.uid];
              }
            } else {
              item.value = this.staffInfoName[item.jname];
            }
            if (item.jname === "maritalStatus") {
              item.value = parseInt(item.value);
            }
          });
        this.$http
          .post("/api/v1.0/client/updateStaffInfo", this.staffInfo)
          .then(res => {
            res = res.body;
            if (res.code === 200) {
              Indicator.close();
              if (this.isEntry) {
                MessageBox("", res.message);
              } else {
                this.$router.push({path: "/signCard"});
              }
            } else {
              MessageBox("", res.message);
              Indicator.close();
            }
          })
          .catch(err => console.log(err));
      },
      // 
      // openPicker(date,type,pos)
      //date01
      //type
      //pos
      //currentDate
      // ========
      openPicker(date, type, pos, currentDate) {
        this.dateType = date;
        this.type = type;
        this.pos = pos;
        if (currentDate) {
          this.nowDateTime = new Date(currentDate);
        } else {
          this.nowDateTime = new Date();
        }
        if (this.dateType === 0) {
          pickerslot[2].style.display = "inline-block";
        } else if (this.dateType === 1) {
          pickerslot[2].style.display = "none";
        }
        for (let i = 0; i < 3; i++) {
          if (this.dateType === 0) {
            pickerslot[i].style.width = "33.33%";
          } else if (this.dateType === 1) {
            pickerslot[i].style.width = "50%";
          }
        }
        this.$refs.picker.open();
      },
      handleConfirm(data) {
        if (data) {
          if (this.dateType === 0) {
            this.selectDateTime = moment(data).format(df1);
          } else if (this.dateType === 1) {
            this.selectDateTime = moment(data).format(df1);
          }
          this.selcetDateTime({
            time: this.selectDateTime,
            type: this.type,
            pos: this.pos
          });
        }
      },
      // ========
      // 
      selcetDateTime(data) {
        if (typeof data.type === "number") {
          // 
          if (data.type === 0) {
            this.staffInfoName.politicsStatus.thePartyTime = new Date(
              data.time.replace(/-/g, "/")
            ).getTime();
          }
          // 
          if (data.type === 1) {
            this.staffInfoName["ResperMessage"].resperst = new Date(
              data.time.replace(/-/g, "/")
            ).getTime();
          }
          // 
          if (data.type === 2) {
            this.staffInfoName["ResperMessage"].resperet = new Date(
              data.time.replace(/-/g, "/")
            ).getTime();
          }
          //
          if (data.type === 3) {
            this.staffInfoName["hasChilds"].childs[
              data.pos
              ].dateOfBirth = new Date(data.time.replace(/-/g, "/")).getTime();
          }
          // 
          if (data.type === 4) {
            this.staffInfoName["eduInfor"].entSchst = new Date(
              data.time.replace(/-/g, "/")
            ).getTime();
          }
          // 
          if (data.type === 5) {
            this.staffInfoName["eduInfor"].entSchet = new Date(
              data.time.replace(/-/g, "/")
            ).getTime();
          }
        } else {
          this.staffInfoName[data.type][0].value = new Date(
            data.time.replace(/-/g, "/")
          ).getTime();
        }
      }
    },
    mounted: function () {
      Vue.Promise
        .all([
          this.$http.get("/api/v1.0/common/config/0"),
          this.$http.get("/api/v1.0/common/config/1"),
          this.$http.get("/api/v1.0/common/config/2"),
          this.$http.get("/api/v1.0/common/config/3"),
          this.$http.get("/api/v1.0/common/config/4"),
          this.$http.get("/api/v1.0/common/config/5"),
          this.$http.get("/api/v1.0/common/config/6"),
          this.$http.get("/api/v1.0/common/config/7"),
          this.$http.get("/api/v1.0/common/config/8"),
          this.$http.get("/api/v1.0/common/config/9"),
          this.$http.get("/api/v1.0/common/config/10"),
          this.$http.get("/api/v1.0/common/config/11"),
          this.$http.get("/api/v1.0/common/config/12"),
          this.$http.get("/api/v1.0/common/config/13"),
          this.$http.get("/api/v1.0/common/config/20")
        ])
        .then(res => {
          this.publicParams = utils.getConfs(res);
          this.typeOfDemiciles = this.publicParams.typeOfDemicileOrg;
          this.nativePlaces = this.publicParams.nativePlaceOrg;
          this.bankNames = this.publicParams.bankNameOrg;
          this.maritalStatuses = this.publicParams.maritalStatusOrg;
          this.nations = this.publicParams.nationOrg;
          this.politicsStatuses = this.publicParams.politicsStatusOrg;
          this.maxinumDeucaLevels = this.publicParams.maxinumDeucaLevelOrg;
          this.diplomaTypes = this.publicParams.diplomaTypeOrg;
          this.staffStatus = this.publicParams.staffStatusOrg;
          // console.log(this.staffStatus);
          this.states = this.publicParams.statesOrg;
          this.probations = this.publicParams.probation;
          this.contractTypes = this.publicParams.contractType;
          // ========
          this.startDate = new Date(
            new Date().getTime() - (365 * 48 + 366 * 16) * 24 * 60 * 60 * 1000
          );
          // ========
          this.status = false;
          Indicator.open("...");
          // 
          this.queryProvinces();
          //
          this.queryStaffInfo();
        });
      // 
      this.$http.get("/api/v1.0/client/findReporter").then(
        response => {
          //
          if (response.body.code === 200) {
            if (response.body.result) {
              this.reportPerson = response.body.result.NAME;
            } else {
              this.reportPerson = "";
            }
          }
        },
        response => {
          console.log("error callback");
        }
      );
    },
    components: {
      uploadImage
    }
  };
</script>

